(function () {
  'use strict';
  angular.module('weather.service',[]).constant('WeatherServiceConstants', {
    KEY: 'b56db60a3d9ab1ee',
    BASE_URL:'http://api.wunderground.com/api/<%= key %>',
    CONDITIONS_URL_TEMPLATE:function() {  return this.BASE_URL + '/conditions/q/<%= latitude %>,<%= longitude %>.json'; },
    HISTORY_URL_TEMPLATE:function() { return this.BASE_URL + '/history_<%= date %>/q/<%= latitude %>,<%= longitude %>.json'}
  });

  angular
    .module('weather.service')
    .factory('WeatherService', WeatherService);


  WeatherService.$inject = ['$http', '$log', 'WeatherServiceConstants'];

  /* @ngInject */
  function WeatherService($http, $log, WeatherConstants) {
    ////////////////


    /* eslint no-undef: 0 */
    function getConditions(latitude, longitude) {
      var urlTemplate =_.template(WeatherConstants.CONDITIONS_URL_TEMPLATE());
      var url = urlTemplate({latitude: latitude, longitude: longitude, key: WeatherConstants.KEY});
      $log.debug('url:' + url);
      return $http.get(url);
    }

    function getHistory(latitude,longitude,date) {
      var urlTemplate =_.template(WeatherConstants.HISTORY_URL_TEMPLATE());
      var url = urlTemplate({latitude: latitude, longitude: longitude, key: WeatherConstants.KEY});
      $log.debug('url:' + url);
      return $http.get(url);
    }


    var service = {
      getConditions: getConditions,
      getHistory:getHistory
    };

    return service;

  }
})();

(function() {
  'use strict';

  angular
    .module('ccapp')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('ccapp')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('ccapp')
    .controller('MainController', MainController);

  /** @ngInject */
  MainController.$inject= ['$scope','$http','uiGmapGoogleMapApi','$log'];
  function MainController($scope,$http,uiGmapGoogleMapApi,$log) {
    var vm = this;
    vm.mapdata = {sites:[]};
    var onPinClick = function() {
      var me = this;
      $scope.$apply(vm.model.showwindow = true);
    }

    var onInfoWindowClosed = function() {
      var me = this;
      $scope.$apply(me.model.showwindow = false);
      InfoBox.prototype.close();
    }


    vm.map = {
      center: {
        latitude: 30.898184,
        longitude: -75.051124
      },
      zoom: 6
    };


    uiGmapGoogleMapApi.then(function(maps){
      $http.get('data/ccdata.json').success(function(data) {

        angular.forEach(data,function(s) {
          var point = {
            showwindow:false,
            coords:{
              latitude: s.latitude,
              longitude: s.longitude
            },
            template:'app/main/window.html',
            onPinClick:onPinClick,
            onInfoWindowClosed:onInfoWindowClosed

          };
          angular.extend(s,point);
        });
        vm.mapdata.sites = data;
      });
    });

    $scope.onClickClose = function(event) {
      $log.log('onClickClose()');
    };

    }
})();

